.header__toggle {
  display: block;
  position: relative;
  top: 4px;
  width: 26px;
  height: 2px;
  margin-right: 30px;
  background-color: #ffffff;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle {
    top: 7px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::before {
    top: 10px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 0px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::after {
    top: 20px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle--closed {
  position: relative;
  left: 5px;
  top: 3px;
  background-color: #F4BCD4;
}
.header__toggle--closed::before,
.header__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
@media (min-width: 768px) {
  .header__toggle--closed::before,
  .header__toggle--closed::after {
    top: 14px;
    left: -2px;
    width: 36px;
    height: 3px;
    background-color: #ffffff;
  }
}
.header__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__toggle--closed::after {
  transform: rotate(-45deg);
}
